.serviceCard {
    position: relative;
    
    width: 400px;
    height: 250px;

    overflow: hidden;
    cursor: pointer;
    border-radius: 10px;
    box-shadow: var(--box-shadow);
}

.serviceName {
    position: absolute;
    /* Altere 'relative' para 'absolute' */
    top: 10px;
    right: 10px;
    z-index: 1;

    font-size: x-large;
    font-weight: bolder;


    color: yellow;
    text-shadow: var(--text-shadow);

}

.serviceImg {
    position: absolute;
    /* Descomente esta linha e altere para 'absolute' */
    width: 100%;
    height: 100%;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    transition: transform .4s;
    /* Animação suave */
    overflow: hidden;
}

.serviceImg:hover {
    transform: scale(1.1);
    /* Zoom */
}

