#depoimentos {
    width: 100%;
    padding: 100px 50px;
    background-color: white;
    color: black;

    flex-flow: column;

    gap: 20px;
}

.slide-show {
    --slide_min_width: 450px;
    --position: calc(var(--slide_min_width) * var(--selected-item) * -1);
    /* width: 100%; */
    /* gap: 20px;   */
}


.slide_button {
    position: relative;
    z-index: 2;
    cursor: pointer;
    padding: 10px;
    border-radius: 50%;
    color: white;
    background-color: var(--main-color-red);
    font-size: 20px;
    box-shadow: var(--box-shadow);
}

.slide_button:hover{
    background-color: var(--li-background);
}


#button_left {
    left: calc(var(--button_space) * -1);
}

#button_right {
    left: var(--button_space);
}


.slider-list {

    width: var(--slide_min_width);

    justify-content: flex-start;

    overflow: hidden;

    transition: left 0.4s ease;
    /* background-color: aqua; */
}


.slide-controller {
    gap: 10px;
}

.slide-controller input[type="radio"] {
    /* Adicione suas regras de estilo aqui */
    appearance: none;
    width: 20px;
    height: 5px;
    background-color: white;
    border-radius: 10px;
    outline: none;
    border: 1px solid var(--main-color-red);}

.slide-controller input[type="radio"]:checked {
    /* Adicione suas regras de estilo para o estado checked aqui */
    background-color: var(--main-color-red);
}


.dep-cards {
    min-width: var(--slide_min_width);
    padding: 5px;
    position: relative;
    left: var(--position);
    transition: all 0.5s ease-out;

    align-items: flex-start;
}


.dep {
    padding: 20px 25px;

    flex-flow: column;
    align-items: flex-start;
    gap: 5px;

    border-radius: 10px;
    background-color: var(--bgLightGray);


}

.dep-person {
    gap: 10px
}

.dep-person>.img {
    width: 60px;
    height: 60px;
    background-color: aqua;
    border-radius: 50%;
}

.dep-person>.person {
    flex-flow: column;
    align-items: flex-start;
}

.dep-person>.person>.name {
    font-weight: 600;
}

.dep-person>.person>.cargo {
    font-size: small;
}

.comments {
    font-size: 40px;
    font-family: 'Space Grotesk', sans-serif;
}