@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&family=Titillium+Web:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital@1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@700&display=swap');

@font-face {
    font-family: peresfont;
    src: url("../src/fonts/neuropolitical-regular.ttf")
}

:root {
    --main-color-red: rgb(180, 0, 0);
    --peresfont: 'peresfont';
    --bgDefault: black;
    --bgLightGray: rgb(200, 200, 200);
    --li-background: rgb(44, 44, 44);

    --textColor: white;
    --border: white;
    --descricao: rgb(50, 50, 50);
    --default-padding: 50px;
    --box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.5);
    --text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
    --inclinacao: -2deg;
    --button_space: 0px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body,
html {
    width: 100%;
    height: 100%;

    color: var(--textColor);
    background-color: var(--background);
    font-family: 'Poppins', sans-serif;

    scroll-behavior: smooth;

}

.socials-icons {
    color: rgb(255, 255, 255);
    cursor: pointer;
}

.flex {
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-items: center;

}

.img {
    background-position: center;
    background-size: 100% 100%;
    background-repeat: no-repeat;

}

/* WPP */

#wpp {
    cursor: pointer;

    position: fixed;
    bottom: 30px;
    left: 30px;

    z-index: 2;
}

#wpp>ion-icon {

    z-index: 2;
    padding: 15px;
    border-radius: 50%;

    background-color: rgb(0, 132, 35);
    color: white;
    font-size: x-large;
}



#orca_wpp {
    padding: 8px 14px;
    padding-left: 50px;

    position: absolute;
    left: 10px;
    z-index: 1;

    border-radius: 20px;

    font-size: large;
    font-weight: 600;

    color: black;
    background-color: lightgray;

}

.main {
    width: 100%;
    background-size: cover;
    background-attachment: fixed;
}

.mainTitle {
    /* --main-color-red */
    color: var(--main-color-red); 
    font-family: var(--peresfont);
    text-transform: uppercase;
    font-size: 40px;
    font-weight: 700;
    text-align: center;
}