#main{
    height: 750px;
    
    background-image: url("../../imgs/main\ img.png");
  
    justify-content: right;
}

#main::after {
    content: "";
    width: 100%;
    height: 750px;
    position: absolute;
    top: 0;
    left: 0;
    
    /* background: rgba(100, 0, 100, 0.4); */
    background: rgba(183, 0, 0, 0.4);
    /* fundo azul transparente */
    backdrop-filter: blur(12px);
 
}


#intro{
    z-index: 1;
    --intro-margin-right: 100px;

    width: 300px;
    padding: 10px 15px;

    margin-right: var(--intro-margin-right);
    
    flex-flow: column;
    align-items: flex-start;
    gap: 20px;
        
    font-size: large;
    font-weight: bold;
    /* background-color: black; */
}