#numeros {
    width: 100%;
    /* min-height: 600px; */


    /* padding: 100px 0; */
    justify-content: center;

    font-weight: bolder;

    color: black;

    position: relative;
}

.numerosTitle>h3 {
    font-size: 40px;
}

.nossosNumeros_container {
    width: 100%;
    height: 100%;
    padding: 100px 0;

    /* padding: 30px; */
    gap: 50px;

    flex-flow: column;
    justify-content: flex-start;
    /* align-items: flex-start; */

    background-color: var(--bgLightGray);
}

.nossosNumeros_content {
    width: 100%;

    flex-flow: row wrap;
    gap: 65px;


}

.numerosImage {
    width: 100%;
    height: 100%;
}


.num {
    min-width: 200px;
    padding: 8px;
    flex-flow: column;

    background-color: white;
    border-radius: 10px;

    box-shadow: var(--box-shadow);
}


.numContent {
    font-size: 40px;
}

