#apresentacao {
    justify-content: space-between;
    padding: 30px 120px;

    width: 100%;
    height: 600px;
    
    background-color: black;

}

#apresentacao::before,
#apresentacao::after {
    content: "";

    width: 100%;
    height: 100px;
    position: absolute;
    top: 720px;
    left: 0;
    background-color: black;
    transform: skewY(var(--inclinacao));
}

#apresentacao::after {
    top: 1320px;
}


#ap-texto {
    width: 450px;

    text-align: left;

    flex-flow: column;
    justify-content: flex-start;
    gap: 10px;
}

#ap-texto>h1 {
    width: 100%;
    text-align: left;
}

#ap-texto>p {
    width: 100%;
    text-align: left;
}

#ap-img {
    width: 400px;
    height: 500px;
    background-color: aliceblue;
    z-index: 1;



    box-shadow: 17px 17px yellow;

}