footer {
    width: 100%;
    padding: 50px 100px;
    padding-bottom: 100px;

    flex-flow: column;
    gap: 100px;

    background-color: var(--main-color-red);
}

.content {
    width: 100%;

    flex-flow: row wrap;
    justify-content: space-between;
    gap: 0px;

    /* background-color: aqua; */
}

.content>.grupoPeres {
    flex-flow: column;
    gap: 10px;
}

.matriz {
    flex-flow: column;
    align-items: flex-start;
    gap: 5px;
}

.matriz>p {
    width: 100%;
}

.footer_icon {

    padding: 8px;
    font-family: peresfont;
    font-size: large;
    color: yellow;
    text-shadow: var(--text-shadow);

    border: 3px solid yellow;
}

.footer_titles {
    width: 100%;
    font-size: x-large;
}

.container {
    flex-flow: column;
    align-items: flex-start;
    gap: 10px;
}


.contatos_footer>ul {
    width: 100%;
    flex-flow: column;
    align-items: flex-start;
}

.contatos_footer>ul>li {
    width: 100%;

    padding: 5px;
    padding-left: 0px;

    justify-content: flex-start;
    gap: 8px;
}

.contatos_footer>ul>li>ion-icon {
    color: rgb(255, 255, 255);
    font-size: larger;
}

#redes-sociais-footer {
    width: 100%;
    margin-top: 10px;
    justify-content: flex-start;

    gap: 30px;
    font-size: large;
}



.divisor {
    width: 100%;
    margin-top: 30px;
    margin-bottom: 10px;
    border: 1px solid white;
}

.reservados {
    font-size: small;
}

.reservados>p>span {
    font-weight: bolder;
}