#nossa-missao {
    width: 100%;
    padding: 0px 50px;
    padding-top: 200px;
    padding-bottom: 100px;

    align-items: flex-start;
    
    gap: 200px;

    color: rgb(255, 255, 255);
    background-color: var(--bgLightGray);
    overflow: hidden;
}

.nm-cards {
    width: 400px;
    /* max-width: 550px; */
    padding: 20px 30px;

    flex-flow: column;
    gap: 30px;

    color: black;
    background-color: white;
    box-shadow: var(--box-shadow);
    border-radius: 20px;

    text-align: center;
    font-weight: 700;
}

.nm-cards>h3 {
    font-weight: 800;
    font-size: 40px;
    color: var(--main-color-red);
}

.nm-cards>ul{
    flex-flow: column;
}